.slider {
  &__arrows {
    position: absolute;
    display: flex;
    width: 72px;
    justify-content: space-between;
    left: 48px;
    bottom: 66px;
  }

  &__arrow {
    position: relative;
    width: 32px;
    height: 32px;
    background-color: seashell;
    border-radius: 50%;

    &--left {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block;
      width: 5.5px;
      height: 9px;
      background: url(../images/slider/arrow-left.svg) center no-repeat;
      background-size: 5.5px 9px;
    }

    &--right {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block;
      width: 5.5px;
      height: 9px;
      background: url(../images/slider/arrow-right.svg) center no-repeat;
      background-size: 5.5px 9px;
    }
  }

  &__description {
    max-width: 464px;
    position: absolute;
    left: 144px;
    bottom: 48px;
  }

  &__title {
    font-family: Poppins, sans-serif;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 8px;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    line-height: 140%;
  }
}
