.header {
  height: 800px;
  position: relative;
  background-color: #008aff;
  border-radius: 0 0 0 300px;
  color: #fff;

  &__wrapper {
    height: 100%;
  }

  &__nav-bar {
    display: flex;
    align-items: center;
    padding-top: 49px;
  }

  &__link {
    font-family: Poppins, sans-serif;
    font-weight: bold;
    font-size: 13px;
    line-height: 150%;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #fff;
    transition: color 0.3s;
    &:hover {
      color: #ff9c8e;
    }
  }

  &__link--active {
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background-color: #ff9c8e;
      position: absolute;
      bottom: -8px;
    }
  }

  &__logo {
    width: 60px;
    height: 28px;
    margin-right: 78px;
  }

  &__info-container {
    height: 723px;
    display: flex;
    align-items: center;
  }

  &__info {
    max-width: 490px;
    height: 355px;
  }

  &__heading {
    font-family: Poppins, sans-serif;
    font-weight: 600;
    font-size: 64px;
    line-height: 68px;
    letter-spacing: -2px;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    line-height: 150%;
    margin: 32px 0 83px;
  }

  &__slider {
    position: absolute;
    left: 57%;
    right: 0;
    bottom: 0;
    height: 680px;
    background: center url(../images/slider/stairs.png) no-repeat;
    background-size: cover;
    border-radius: 200px 0 0;
  }
}
